@import url(https://fonts.googleapis.com/css2?family=Overpass:wght@400;600&display=swap);
.overlay { 
    position: fixed;
    z-index: 3;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(52,52,52, .6);
    transition: all .2s linear;
    animation-name: Overlay-animation;
    animation-duration: .2s;
    animation-timing-function: linear;
}

@keyframes Overlay-animation{
    from { opacity: .2;}
    to {opacity: 1;}
}

.Modal {
    position: absolute;
    z-index: 4;
    top: 20%;
    left: 0;
    right: 0;
    width: 55vw;
    bottom: 20%;
    margin: auto;
    background: none;
    text-align: right;
}

.Modal__header{
    display: flex;
    background: none;
    color: #fff;
}
.Modal__title{
    display: block;
    margin-right: auto;
    font-size: 1.5em;
}

.Modal__body{
    display: flex;
    background-color: rgb(238, 238, 238);
    width: 100%;
    height: 100%;
    margin: auto;
    text-align: left;
}
.Modal__img {
    width: 40%;
}

.Modal__content {
    width: 60%;
    padding-right: 5px;
}

.Modal__content hr{
    margin: 30px 0;
}

.Modal__content h4 {
    margin-bottom: 5px;
}

.Modal__description {
    word-wrap: break-word;
}

.Modal__price {
    font-size: 2rem;
}

.Modal__header button{
    font-size: 1rem;
    transform: scale(1.4);
    font-weight: bold;
    background: none;
    border: none;
    color: #fff;
    cursor: pointer;
}

.Modal__header button:hover{
    color: rgb(240, 18, 18);
}

.zoom {
    transition: ease .2s;
    cursor: zoom-in;

}
.zoom:active {
    transform: scale(1.6);
}

:root {
  --main-color: #ef233c;;
  --second-color: #2a2a2a;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: #ddd;
  font-family: 'Overpass', sans-serif;
}
.App header {
  height: 60vh;
  background: url('https://images.unsplash.com/photo-1627483262112-039e9a0a0f16?ixlib=rb-1.2.1&ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&auto=format&fit=crop&w=1170&q=80') center/100% no-repeat;
}
header img {
  width: 100%
  
}
nav {
  padding: 10px;
  z-index: 2;
  text-align: right;
  background-color: #2a2a2a;
  background-color: var(--second-color);
}

ul {
  margin-right: 100px;
}

li {
  display: inline-block;
  margin: 0 20px;
  font-size: 1.05rem;
}


a {
  text-decoration: none;
  color: #ffffff;
}

a:hover{
  color: #ef233c;
  color: var(--main-color);
}

h1 {
  color: #ffff;
  padding: 20px;
}

main {
  max-width: 1000px;
  margin: auto;
}

.product {
  width: 300px;
  border-radius: 4px;
  box-shadow: 0px 0px 20px -9px rgba(0,0,0,0.75);
  background-color: #eee;
  cursor: pointer;
  transition: transform .2s ease;

}

.product:hover {
  transform: scale(1.01);
}

.product img {
  border-radius: 4px 4px 0px 0px;
  box-shadow: 0px 0px 20px -9px rgba(0,0,0,0.75);
  width: 100%;
}

.info {
  color: #2a2a2a;
  color: var(--second-color);
  padding: 10px;
  text-align: center;
  
}

.price {
  color: #ef233c;
  color: var(--main-color);
  font-size: 1.5rem;
}

section {
  display: flex;
  padding: 20px;
  display: grid;
  grid-template-columns: repeat(3, 300px);
  grid-gap: 20px;
  justify-content: center;
  align-content: center;
}
